<template>
  <ApolloQuery
    :query="require('../graphql/queries/getAdminProfile.gql')"
    class="row ma-sm-4"
  >
    <template v-slot="{ result: { error, data }, isLoading }">
      <!-- Loading -->
      <div
        v-if="isLoading === 1"
        class="mx-auto"
      >
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <!-- Error -->
      <AppNotification
        v-else-if="error"
        :message="error.message"
      />

      <!-- Result -->
      <template v-else-if="data">
        <v-col v-if="!data.getAdminProfile.permissions.includes('DASHBOARD_ADMINISTRATION') && data.getAdminProfile.role !== 'SUPERUSER'">
          <v-alert
            outlined
            type="error"
          >
            Этот раздел вам недоступен!
          </v-alert>
        </v-col>

        <template v-else>
          <v-col cols="12">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dateRangeText"
                  solo
                  flat
                  hide-details="auto"
                  background-color="#464646"
                  label="Диапазон дат"
                  readonly
                  class="rounded-lg"
                  v-on="on"
                >
                  <template v-slot:prepend-inner>
                    <v-icon class="mr-1">
                      mdi-calendar
                    </v-icon>
                  </template>
                </v-text-field>
              </template>
              <v-date-picker
                v-model="dates"
                no-title
                scrollable
                range
              >
                <v-spacer />

                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  Отмена
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="saveDates"
                >
                  ОК
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col>
            <ApolloQuery
              :query="require('../graphql/queries/getDashboardInfo.gql')"
              :variables="{
                dateFrom,
                dateTo,
              }"
            >
              <template v-slot="{ result: { error: dashboardError, data: dashboardData }, isLoading: dashboardIsLoading }">
                <!-- Error -->
                <AppNotification
                  v-if="dashboardError"
                  :message="dashboardError.message"
                />

                <!-- Result -->
                <v-row v-else-if="dashboardData || dashboardIsLoading === 1">
                  <v-col
                    v-for="item of cards"
                    :key="item.value"
                    :cols="item.value === 'ngr' ? '12' : null"
                  >
                    <v-card
                      :loading="dashboardIsLoading === 1"
                      flat
                      class="px-3"
                    >
                      <v-card-title>{{ item.text }}</v-card-title>
                      <v-card-text class="text-h3 white--text font-weight-bold">
                        <span v-if="dashboardData">{{ dashboardData.getDashboardInfo[item.value] }}</span>
                        <span v-else>?</span>
                        <span class="text-subtitle-2 font-weight-bold ml-1">RUB</span>
                      </v-card-text>
                      <template v-if="item.value === 'promotions'">
                        <v-btn
                          icon
                          absolute
                          top
                          right
                          @click="show = !show"
                        >
                          <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                        </v-btn>
                        <v-expand-transition>
                          <div v-show="show">
                            <v-divider />

                            <v-card-text>
                              <v-list-item
                                v-for="expense of expenses"
                                :key="expense.value"
                                two-line
                              >
                                <v-list-item-content>
                                  <v-list-item-title>{{ expense.text }}</v-list-item-title>
                                  <v-list-item-subtitle v-if="dashboardData">
                                    {{ dashboardData.getDashboardInfo[expense.value] }}
                                  </v-list-item-subtitle>
                                  <v-list-item-subtitle v-else>
                                    ?
                                  </v-list-item-subtitle>
                                </v-list-item-content>
                              </v-list-item>
                            </v-card-text>
                          </div>
                        </v-expand-transition>
                      </template>
                    </v-card>
                  </v-col>

                  <v-col
                    cols="12"
                    class="text-center text-h5"
                  >
                    Игры
                  </v-col>

                  <v-col>
                    <v-card
                      :loading="dashboardIsLoading === 1"
                      flat
                      class="px-4 pb-2"
                    >
                      <v-data-table
                        v-if="dashboardData"
                        :headers="headers"
                        :items="dashboardData.getDashboardInfo.gamesRevenue"
                        :items-per-page="5"
                        no-data-text="Статистика по режимам отсутствует"
                      />
                    </v-card>
                  </v-col>
                </v-row>

                <!-- No result -->
                <AppNotification
                  v-else
                  message="Нет результата :("
                />
              </template>
            </ApolloQuery>
          </v-col>
        </template>
      </template>

      <!-- No result -->
      <AppNotification
        v-else
        message="Нет результата :("
      />
    </template>
  </ApolloQuery>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      menu: false,
      dates: [],
      dateFrom: '',
      dateTo: '',
      cards: [
        {
          text: 'Поступления (депозиты)',
          value: 'deposits',
        },
        {
          text: 'Расходы (выводы)',
          value: 'withdraws',
        },
        {
          text: 'Иные расходы (по умолч. промо)',
          value: 'promotions',
        },
        {
          text: 'Доход (NGR)',
          value: 'ngr',
        },
      ],
      show: false,
      expenses: [
        {
          text: 'Бонусы',
          value: 'bonuses',
        },
        {
          text: 'Подарки',
          value: 'gifts',
        },
        {
          text: 'Рефералка',
          value: 'referrals',
        },
        {
          text: 'Комиссия платёжной системы',
          value: 'commissions',
        },
      ],
      headers: [
        { text: 'Режим', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во ставок:', value: 'betsQty', class: 'white--text text-subtitle-2' },
        { text: 'Сумма ставок:', value: 'betsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Выиграно:', value: 'winsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Проиграно:', value: 'looseAmount', class: 'white--text text-subtitle-2' },
        { text: 'GGR:', value: 'ggr', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.dateFrom, this.dateTo] = this.dates;
    },
  },
};
</script>
